* {
  box-sizing: border-box;
  
}

body {
  background-color: #393E46;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
}

/*bmi container, title, labels, inputs*/
.bmi-title {
  color: #FFD369;
  font-size: 35px;
  font-weight: bolder;
  letter-spacing: 1.4px;
  padding-top: 70px;
}

.bmi-box {
  background-color: #FFD369;
  border: 1px solid #FFD369;
  border-radius: 50px 0px 50px 0px;
  margin: 0 auto 2px auto;
  padding: 40px;
  width: 500px;
}

.bmi-label {
  font-weight: 600;
  letter-spacing: 0.2px;
  text-align: left;
}

#height-feet {
  margin-bottom: 5px;
}

#height-feet::-webkit-outer-spin-button,
#height-feet::-webkit-inner-spin-button,
#height-inches::-webkit-outer-spin-button,
#height-inches::-webkit-inner-spin-button,
#weight-lbs::-webkit-outer-spin-button,
#weight-lbs::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

#height-feet[type=number],
#height-inches[type=number],
#weight-lbs[type=number] {
  -moz-appearance: textfield;
}

#height-feet, #height-inches, #weight-lbs{
  background-color: #F5F5F5;
  border: none;
  font-size: medium;
}
#height-feet:hover, #height-inches:hover, #weight-lbs:hover{
  border: 2px solid #393E46;
}

#weight-lbs {
  margin-bottom: 20px;
}

/*bmi number*/
.bmi-number-box {
  background-color: #dcdcdc;
  border-radius: 5px;
  height: 45px;
  margin: 30px 0 25px 0;
  overflow: hidden;
}


.bmi-number {
  font-size: 30px;
}

/*bmi text*/
.bmi-str-box {
  height: 100%;
  font-size: 25px;
  width: 100%;
}
.bmi-str{
  font-weight: bolder;
}
.res{
  color: rgb(100, 100, 100);
  font-size: 18px;
}
/*button*/
.results-btn {
  border-radius: 25px;
  background-color: #222831;
  border-color: #222831;
  letter-spacing: 0.6px;
  color: white;
}

.results-btn:hover {
  background-color: #393E46;
  border-color: #393E46;
}

.results-text {
  color:white;
}

.results-text:hover {
  color: #fff;
}

/*footer*/
.author-ftr {
  color: #fff;
  padding-top: 2px;
}

.author {
  margin-top: 50px;
  font-size: 15px;
}
.header{
  color: #FFD369;
  padding-top: 100px;
  border-radius: 25px;
  margin: 0 auto 2px auto;
  padding: 40px;
  width: 500px;
}
.content{
  
  color: #222831;
  background-color: #FFD369;
  padding: 25px;
  text-align: justify;
}
.nav{
  width: 100%;
  height: 50px;
  background-color: #FFD369;
}
.a, .b{
  color: #222831;
  background-color: #ffedc3;
  padding: 25px;
  text-align: justify;
  
}
.a{
  border-radius: 50px 0px 50px 0px;
}
.b{
  border-radius: 0px 50px 0px 50px;
}